import { Container, Grid, Paper } from "@mui/material";
import React, { useEffect, useState } from "react";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import CryptoBlock, { DisabledCard } from "../../../components/CryptoBlock";
import CryptoSkeleton from "../../../components/CryptoSkeleton";
import { useDispatch, useSelector } from "react-redux";
import { setLoading } from "../../../redux/reducers/withdraw";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const Withdrawal = ({ t, setVerificationPush, mode }) => {
  const dispatch = useDispatch();

  const [filter, setFilter] = useState("");

  const { СurrenciesList, СurrenciesLoading } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const account = profileData?.data?.profile?.verification?.value;

  const { isLoading } = useSelector(({ withdrawReducer }) => withdrawReducer);
  const filteredCoin = React.useMemo(
    () =>
      СurrenciesList.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const Сurrencies = filter ? filteredCoin : СurrenciesList;

  useEffect(() => {
    if (!isLoading) {
      dispatch(setLoading(true));
    }

    dispatch(getСurrenciesList());
    // eslint-disable-next-line
  }, []);

  return (
    <Paper component="section">
      <Container>
        <Balance />
        <CabinetTop
          title={t("WithdrawFunds")}
          t={t}
          input
          setFilter={setFilter}
        />
        <Grid container spacing={2}>
          {!СurrenciesLoading
            ? Array.from(Array(12).keys()).map((item, idx) => (
                <Grid key={idx} item xs={12} sm={6} md={3} lg={3} xl={3}>
                  <CryptoSkeleton style={{ width: 389 }} />
                </Grid>
              ))
            : Сurrencies
                .filter((item) => item.can_withdraw)
                .map((item, idx) => (
                  <Grid
                    item
                    xs={12}
                    sm={6}
                    md={3}
                    lg={3}
                    xl={3}
                    key={idx}
                    onClick={() => {
                      localStorage.setItem("sellDetail", JSON.stringify(item));
                    }}
                  >
                    {account == 2 ? (
                      <CryptoBlock
                        mode={mode}
                        t={t}
                        setVerificationPush={setVerificationPush}
                        item={item}
                        href={`/dashboard/withdraw/${item.currency}`}
                      />
                    ) : (
                      <DisabledCard
                        mode={mode}
                        t={t}
                        setVerificationPush={setVerificationPush}
                        item={item}
                      />
                    )}
                  </Grid>
                ))}
        </Grid>
      </Container>
    </Paper>
  );
};

export default Withdrawal;
